import Vue from 'vue'
import Router, {
	RouterMount
} from 'uni-simple-router';
import modules from './modules'
Vue.use(Router)
//
const router = new Router({
	routes: [...modules]//
});
let token = ''
uni.getStorage('token').then(res=>token=res.data)

//
router.beforeEach(async (to, from, next) => {
	if(to.path!=='/pages/login/login'){
		next({
			path: '/pages/login/login',
			query: {
				msg: 'tab54',
			},
			NAVTYPE: 'push'
		});
	} else {
		// next({
		// 	name: 'login',
		// 	params: {
		// 		msg: 'tab54',
		// 	},
		// 	NAVTYPE: 'push'
		// })
		next()
	}

})
// 
router.afterEach((to, from) => {
	console.log('afterEach----')
})

export {
	RouterMount,
	router
}
